class Memba {
    constructor(first_name, last_name, key, father_name, mother_name, dob, pob, number, address, spouse_name, native_place, education, job){
        this.key = key
        this.first_name = first_name
        this.last_name = last_name
        this.father_name = father_name
        this.mother_name = mother_name
        this.dob = dob
        this.pob = pob
        this.number = number
        this.address = address
        this.spouse_name = spouse_name
        this.native_place = native_place
        this.education = education
        this.job = job
    }
}

export default Memba;