import Memba from './memba';
import Node from './treeNode';

function Tree() {
    //attributes= ["first_name", "father_name", "mother_name", "last_name", "dob", "pob", "number", "spouse_info", "address", "native_place", "educ_info", "job_info", "f", "note", "email"]
    const data = require('./test.json');
    const nodes = [];
    const names = [];

    //create nodes
    for (let i = 0; i < data.length; i += 1) {
        const json_memba = data[i];
        const memba = new Memba(json_memba.first_name, json_memba.last_name, i, json_memba.father_name, json_memba.mother_name, json_memba.dob, json_memba.pob, json_memba.number, json_memba.address, json_memba.spouse_info, json_memba.native_place, json_memba.educ_info, json_memba.job)
        const node = new Node(memba);
        nodes.push(node);
        names.push(node.data.first_name.split(" ")[0].trim())
    }

    console.log(names);
    //add mother,father,children
    for (let i = 0; i < nodes.length; i += 1) {
        const mem = nodes[i];
        const father_first_name = mem.data.father_name.split(" ")[0];
        // console.log("Looking for father: "+ father_first_name);
        if (names.includes(father_first_name)) {
            // console.log("Father found");
            let index = names.indexOf(father_first_name);
            nodes[index].children.push(mem);
            nodes[index].children[nodes[index].children.length - 1].father = nodes[index];
        }


        if (names.includes(mem.data.mother_name.split(" ")[0])) {
            let index = names.indexOf(mem.data.mother_name.split(" ")[0]);
            nodes[index].children.push(mem);
            nodes[index].children[nodes[index].children.length - 1].mother = nodes[index];
        }

        if (names.includes(mem.data.spouse_name.split(" ")[0])) {
            if (mem.spouse == null) {
                let index = names.indexOf(mem.data.spouse_name.split(" ")[0]);
                nodes[index].spouse = mem;
                nodes[index].spouse.spouse = nodes[index];
            }
        }
    }



    return nodes;
}

export default Tree;