import React, { useState } from 'react';
import './App.css';
import Tree from './DataStructure/tree';
import Toolbar from './toolbar';
import styles from './toolbar.module.css';
import Output from './DataStructure/output';
import Modal from './UI/Modal/Modal';
import Menu from './UI/Menu/Menu';

const nodes = new Tree();
const USER_NAME = "admin";
const PASS = "pbinfo";

function App() {
  const [wordToSearch, setWordToSearch] = useState("");
  const [output, setOutput] = useState([]);
  const [authorized, setAuthorized] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const login = () => {
    if (username === USER_NAME && password == PASS) {
      setAuthorized(true);
    }
  }

  const usernameInputHandler = (event) => {
    setUsername(event.target.value);
  }

  const passwordInputHandler = (event) => {
    setPassword(event.target.value);
  }

  const searchName = () => {
    const copyArr = [];

    for (let i = 0; i < nodes.length; i += 1) {
      const mem = nodes[i].data;
      const full_name = (mem.first_name + " " + mem.last_name).toLowerCase();
      if (full_name.includes(wordToSearch.toLowerCase())) {
        copyArr.push(nodes[i]);
      }
    }
    return copyArr;
  }

  const searchAddress = () => {
    const copyArr = [];
    for (let i = 0; i < nodes.length; i += 1) {
      const mem = nodes[i].data;
      const address = mem.address.toLowerCase();
      if (address.includes(wordToSearch.toLowerCase())) {
        // console.log(mem.address);
        copyArr.push(nodes[i]);
        // console.log(copyArr);
      }
    }
    return copyArr;
  }


  const submitPressedHandler = (typeOfSearch) => {
    setOutput([]);
    let copyArr = [];
    if (typeOfSearch === "Search by name") {
      copyArr = searchName();
    }
    else if (typeOfSearch === "Search by address") {
      copyArr = searchAddress();
    }
    setOutput(copyArr);
  }

  const getRelative = (node, relation) => {
    if (output.indexOf(node[relation]) === -1) {
      if (node[relation] != null) {
        let copyArr = [...output, node[relation]];
        setOutput(copyArr);
      }
    }
  }

  const getChildrenHandler = (node) => {
    let copyArr = [...output];
    for (let i = 0; i < node.children.length; i++) {
      const child = node.children[i];
      if (output.indexOf(child) === -1) {
        copyArr.push(child);
      }
    }
    setOutput(copyArr);
  }

  const getAncestors = (node) => {
    // console.log("Entered getAncetors");
    setOutput([]);
    let copyArr = [node];
    let curr = node;
    // console.log(curr.father);
    while (curr.father !== null) {
      curr = curr.father;
      copyArr.unshift(curr);
      // console.log(curr.data.first_name);
    }
    setOutput(copyArr);
  }


  const nameInputHandler = (event) => {
    setWordToSearch(event.target.value);
  }

  const clearPressedHandler = () => {
    setOutput(null);
    setOutput([]);
  }

  return (
    <div className="App">
      <Menu/>
      <br/>
      <br/>
      <br/>
      <Toolbar
        className= {styles.Toolbar}
        nameInputHandler={nameInputHandler}
        submitPressedHandler={submitPressedHandler}
        clearPressedHandler={clearPressedHandler}
      />
      <Modal
        show={!authorized}
        modalClosed={login}>
        <label>Username:  </label>
        <input type="text" onChange={usernameInputHandler} />
        <br />
        <label>Password:  </label>
        <input type="password" onChange={passwordInputHandler} />
        <br />
        <br />
        <button onClick={login}>Login</button>
      </Modal>
      <Output persons={output}
        getChildrenHandler={getChildrenHandler}
        getRelative={getRelative}
        getAncestors={getAncestors} />

    </div>
  );
}
export default App;