import React,{useState} from 'react';
const Toolbar = (props) => {
    const [typeOfSearch, setTypeOfSearch] = useState ("Search by name");

    const handleSelection = (event) => {
        setTypeOfSearch(event.target.value);
    }

    return <div>
        <hr />
        <input type="text" onChange={props.nameInputHandler} />
        <select value = {typeOfSearch} onChange= {handleSelection}>
            <option>Search by name</option>
            <option>Search by address</option>
        </select>
        <br />
        <br />
        <button onClick={() => props.submitPressedHandler (typeOfSearch)}>Submit</button>
        <button onClick={props.clearPressedHandler}>Clear</button>
    </div>
}

export default Toolbar;